button {
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  transition: all 0.25s ease-in-out;
}

.btn:focus, .btn:active {
  outline: none;
}

.btn--block {
  display: block;
  width: 100%;
}

.btn-primary {
  background: #F6BA20;
  color: white;
  font-size: 14px;
  font-weight: 700;
  font-family: "Noto Sans", sans-serif;
  border-radius: 4px;
  text-transform: uppercase;
  letter-spacing: 0.056em;
  box-shadow: 0px 5px 15px rgba(217, 216, 216, 0.5);
  padding: 17px;
}

.btn-primary:hover {
  background: #da9f09;
  box-shadow: none;
}

.btn-outline {
  background: transparent;
  color: #2A2A2A;
  font-size: 14px;
  font-weight: 700;
  border-radius: 4px;
  text-transform: lowercase;
  letter-spacing: 0.032em;
  border: 1px solid rgba(42, 42, 42, 0.5);
  padding: 6px 10px;
}

.btn-outline:hover {
  background: #2A2A2A;
  color: white;
  box-shadow: none;
}

.btn-white {
  background: white;
  color: #2A2A2A;
  font-family: "Noto Sans", sans-serif;
  font-size: 14px;
  font-weight: 700;
  border-radius: 8px;
  text-transform: lowercase;
  letter-spacing: 0.1em;
  border: 1px solid transparent;
  text-transform: uppercase;
  padding: 9px 35px;
}

.btn-white:hover {
  background: #F6BA20;
  color: white;
  box-shadow: none;
}

.icon {
  display: inline-block;
  font-style: normal;
  content: ' ';
}

.ico {
  fill: currentColor;
}

.ui-group {
  position: relative;
  margin-bottom: 20px;
}

.ui-group label {
  display: block;
  margin-bottom: 10px;
  color: rgba(42, 42, 42, 0.5);
  font-size: 12px;
}

.ui-group input, .ui-group textarea {
  display: block;
  width: 100%;
  -webkit-appearance: none;
  background: white;
  color: #2A2A2A;
  border: 2px solid transparent;
  border-radius: 4px;
  font-size: 16px;
  padding: 14px 20px;
  transition: 0.25s ease-in-out;
}

.ui-group input::placeholder, .ui-group textarea::placeholder {
  color: rgba(42, 42, 42, 0.5);
}

.ui-group input:focus, .ui-group textarea:focus {
  border-color: #F6BA20;
}

.ui-group--search input {
  padding-left: 50px;
}

.ui-group--search .ico {
  position: absolute;
  left: 15px;
  top: 50%;
  z-index: 5;
  transform: translateY(-50%);
}

.ui-group--search .ico-search {
  font-size: 22px;
  fill: rgba(42, 42, 42, 0.3);
}

.ui-group textarea {
  resize: vertical;
}

.ui-input__validation {
  display: inline-block;
  margin-top: 3px;
  color: #f45b63;
  font-size: 12px;
}

.ui-checkbox {
  width: 100%;
}

.ui-checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.ui-checkbox input:checked + label::before {
  border-color: #F6BA20;
}

.ui-checkbox input:checked + label::after {
  transform: translateY(-50%) scale(1);
}

.ui-checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
  color: rgba(42, 42, 42, 0.5);
  transition: 0.25s ease-in-out;
}

.ui-checkbox label::before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 14px;
  background-color: white;
  border: 1px solid rgba(42, 42, 42, 0.2);
  border-radius: 3px;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.ui-checkbox label::after {
  position: absolute;
  left: 6px;
  top: 50%;
  transform: translateY(-50%) scale(0);
  display: inline-block;
  content: ' ';
  size: 8px;
  background: #F6BA20;
  border-radius: 50%;
  transition: transform .2s ease;
}

.ui-checkbox label span {
  display: inline-block;
  padding-left: 35px;
  vertical-align: top;
  line-height: 1.3;
  font-size: 14px;
  cursor: pointer;
}

.ui-checkbox label:hover {
  color: rgba(42, 42, 42, 0.7);
}

.ui-checkbox--right {
  margin-top: 13px;
}

.ui-checkbox--right label::before {
  left: auto;
  right: 0;
}

.ui-checkbox--right label::after {
  left: auto;
  right: 4.5px;
}

.ui-checkbox--right label span {
  padding-left: 0;
  padding-right: 35px;
}

/* HEADER */
.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background: white;
}

.header-container {
  border-bottom: 1px solid #E6E6E6;
}

.header__wrapper {
  display: flex;
  align-items: center;
  height: 60px;
  color: #2A2A2A;
}

.header__logo {
  position: relative;
  flex: 0 0 auto;
  font-size: 0;
  padding-right: 30px;
  color: #2A2A2A;
  transition: 0.25s ease-in-out;
}

.header__logo .ico {
  font-size: 29px;
  overflow: hidden;
}

.header__logo:hover {
  opacity: .8;
}

.header__cta {
  margin-left: auto;
  padding-left: 30px;
  display: flex;
  align-items: center;
}

.header__user {
  padding-left: 20px;
}

.header__user-wrap {
  display: flex;
  align-items: center;
}

.header__user-name {
  font-size: 14px;
  font-weight: 700;
  margin-right: 20px;
}

.header__user-avatar {
  position: relative;
  z-index: 1;
  border-radius: 50%;
  overflow: hidden;
  size: 40px;
  flex: 0 0 40px;
  font-size: 0;
  user-select: none;
  pointer-events: none;
}

.header__user-avatar img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.header.is-fixed {
  transform: translate3d(0, -100%, 0);
  background: white;
  transition: 0.25s ease-in-out;
}

.header.is-fixed .header__logo {
  color: #2A2A2A;
}

.header.is-fixed .header__wrapper {
  color: #2A2A2A;
}

.header.is-fixed-visible {
  position: fixed;
  transform: translate3d(0, 0, 0);
}

@media print, (max-width: 992px) {
  .header__wrapper {
    height: 48px;
  }
  .header__logo {
    padding-right: 0;
  }
  .header__logo .ico {
    font-size: 24px;
  }
}

.footer {
  color: #A7A7A7;
  background: #F6F6F6;
  padding: 20px 0;
}

.footer__wrapper {
  display: flex;
  align-items: center;
}

.footer__credentials {
  font-size: 10px;
  line-height: 2;
