  margin-bottom: 20px;
  color: rgba(42, 42, 42, 0.5);
}

.pagination__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.032em;
}

.pagination__list li {
  display: inline-block;
  margin-right: 10px;
}

.pagination__list li:last-child {
  margin-right: 0;
}

.pagination__list li.is-active a {
