// UI


// WORKS KIND OF bootstrap - group have label and input
.ui-group {
  position: relative;
  margin-bottom: 20px;
  label {
    display: block;
    margin-bottom: 10px;
    color: rgba($fontColor, .5);
    font-size: 12px; }

  input, textarea {
    display: block;
    width: 100%;
    -webkit-appearance: none;
    background: white;
    color: $fontColor;
    border: 2px solid transparent;
    border-radius: 4px;
    font-size: 16px;
    padding: 14px 20px;
    transition: .25s $ease;
    &::placeholder {
      color: rgba($fontColor, .5); }
    &:focus {
      border-color: $colorPrimary; } }
  &--search {
    input {
      padding-left: 50px; }
    .ico {
      position: absolute;
      left: 15px;
      top: 50%;
      z-index: 5;
      transform: translateY(-50%);
      &-search {
        font-size: 22px;
        fill: rgba($fontColor, .3); } } }
  textarea {
    resize: vertical; } }

.ui-input__validation {
  display: inline-block;
  margin-top: 3px;
  color: $colorRed;
  font-size: 12px; }

// Checkbox
.ui-checkbox {
  width: 100%;
  input {
    display: none;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    &:checked {
      + label {
        &::before {
          border-color: $colorPrimary; }
        &::after {
          transform: translateY(-50%) scale(1); } } } }
  label {
    display: block;
    width: 100%;
    position: relative;
    cursor: pointer;
    color: rgba($fontColor, .5);
    transition: .25s $ease;
    &::before {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      content: ' ';
      size: 14px;
      background-color: white;
      border: 1px solid rgba($fontColor, .2);
      border-radius: 3px;
      cursor: pointer;
      vertical-align: middle;
      cursor: pointer;
      transition: all $transitionSpeed ease-in-out; }
    &::after {
      position: absolute;
      left: 6px;
      top: 50%;
      transform: translateY(-50%) scale(0);
      display: inline-block;
      content: ' ';
      size: 8px;
      background: $colorPrimary;
      border-radius: 50%;
      // background-image: svg-load('../../images/svg/checkbox.svg')
      // background-repeat: no-repeat
      // background-position: center
      transition: transform .2s ease; }
    span {
      display: inline-block;
      padding-left: 35px;
      vertical-align: top;
      line-height: 1.3;
      font-size: 14px;
      cursor: pointer; }
    &:hover {
      color: rgba($fontColor, .7); } }
  &--right {
    margin-top: 13px;
    label {
      &::before {
        left: auto;
        right: 0; }
      &::after {
        left: auto;
        right: 4.5px; }

      span {
        padding-left: 0;
        padding-right: 35px; } } } }
