/*======================================
 *  Selectric v1.13.0
 *====================================== */

.selectric-wrapper {
  position: relative;
  cursor: pointer; }

.selectric-responsive {
  width: 100%; }

.selectric {
  border: 2px solid transparent;
  border-radius: 4px;
  background: white;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  transition: .25s $ease;
  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    margin: 0 25px 0 0px;
    font-size: 16px;
    letter-spacing: 0.04em;
    padding: 14px 20px;
    color: $fontColor;
    user-select: none; }
  .button {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50%;
    right: 5px;
    margin-left: auto;
    // display: block
    top: 50%;
    width: 25px;
    height: 25px;
    font-size: 6px;
    color: $fontColor;
    transform: translateY(-50%) rotate(180deg);
    transition: .25s $ease; } }

.selectric-focus .selectric {
 }  // border-color: $colorPrimary

.selectric-hover .selectric {
  // border-color: #c4c4c4
  .button {
    color: #a2a2a2; } }


.selectric-open {
  z-index: 9999;
  .selectric {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    // border-color: $colorPrimary
    .button {
      transform: translateY(-50%); } }

  .selectric-items {
    display: block; } }

.selectric-disabled {
  filter: alpha(opacity = 50);
  opacity: 0.5;
  cursor: default;
  user-select: none; }

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
  select {
    position: absolute;
    left: -100%; }
  &.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    select {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: none;
      z-index: 1;
      box-sizing: border-box;
      opacity: 0; } } }

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important; }

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important; }

/* Items box */

.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: white;
  border: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  overflow: hidden;
  z-index: -1;
  box-shadow: 0 0 10px -6px;
  .selectric-scroll {
    height: 100%;
    overflow: auto; } }

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%; }

.selectric-items {
  ul {
    list-style: none;
    padding: 0;
    margin: 0; }
  li {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 16px;
    padding: 15px;
    display: block;
    color: $fontColor;
    cursor: pointer;
    transition: .25s $ease;
    &.selected {
      background: #FAFAFC; }

    &.highlighted {
      background: #FAFAFC; }
    &:hover {
      background: #FAFAFC; } }
  .disabled {
    filter: alpha(opacity = 50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      background: none;
      color: #444; }
    &.disabled li {
      filter: alpha(opacity = 100);
      opacity: 1; }
    li {
      padding-left: 25px; } } }


.selectric-bordered {
  .selectric {
    border: 1px solid #F6F6F6;
    // border-radius: 4px
    .label {
      font-size: 14px;
      padding: 12px 20px; }
    .button {
      font-size: 8px;
      color: rgba(#2A2A2A, .4); } }
  &.selectric-hover .selectric {
    .button {
      color: rgba(#2A2A2A, .6); } } }
